.hero {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 27px;
  background: image-set(url("../img/index/background-hero@1x.webp") type("image/webp") 1x,
    url("../img/index/background-hero@2x.webp") type("image/webp") 2x,
    url("../img/index/background-hero@1x.jpg") 1x,
    url("../img/index/background-hero@2x.jpg") 2x),
    linear-gradient(180deg, #63a63a 63%, transparent 63%);
  background-repeat: no-repeat;
  color: $primary__fonts-color;
  background-position: top, top;

  @media (min-width: $tablet-width) {
    background: none;
    padding-top: 65px;
  }

  @media (min-width: $desktop-width) {
    padding-top: 110px;
    padding-bottom: 184px;
    display: block;
  }
}

.hero::before {
  @media (min-width: $desktop-width) {
    content: "";
    position: absolute;
    background: image-set(url("../img/index/background-hero-desktop@1x.webp") type("image/webp") 1x,
      url("../img/index/background-hero-desktop@2x.webp") type("image/webp") 2x,
      url("../img/index/background-hero-desktop@1x.jpg") 1x,
      url("../img/index/background-hero-desktop@2x.jpg") 2x),
      linear-gradient(270deg, #63a63a 50%, transparent 50%);
    background-repeat: no-repeat;
    background-position: left calc(50% + 521px) bottom, center;
    width: 100%;
    bottom: 0;
    top: -50%;
    z-index: -2;
  }
}

.hero__wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    margin-left: 90px;
    align-items: flex-start;
  }

  @media (min-width: $desktop-width) {
    margin-left: 80px;
    position: relative;
    max-width: 570px;
  }
}

.hero__title {
  @include fonts(36px, 36px);
  color: $special__fonts-color;
  margin: 0 0 25px 0;
  text-align: center;
  min-height: 72px;

  @media (min-width: $tablet-width) {
    @include fonts(60px, 60px);
    margin: 0 0 40px 0;
    text-align: left;
    max-width: 75%;
  }
}

.hero__slogan {
  @include fonts(14px, 14px);
  text-transform: uppercase;
  color: $special__fonts-color;
  margin: 0 0 29px 0;

  @media (min-width: $tablet-width) {
    @include fonts(20px, 20px);
    margin-bottom: 50px;
    max-width: 50%;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 52px;
  }
}

.hero__cover {
  margin-bottom: 3px;

  @media (min-width: $tablet-width) {
    order: 1;
    align-self: center;
    margin: -83px 0 0 -90px;
    position: relative;
    z-index: -1;
  }

  @media (min-width: $desktop-width) {
    position: absolute;
    left: 80%;
    z-index: 1;
  }
}

.hero__button {
  width: 100%;

  @media (min-width: $tablet-width) {
    max-width: 244px;
    padding: 14px 26px;
  }

  @media (min-width: $desktop-width) {
    max-width: 244px;
    padding: 14px 26px;
  }
}
