.main-header__toggle {
  position: relative;
  border: none;
  cursor: pointer;
  background-color: transparent;
  width: 24px;
  height: 18px;
  display: flex;
  align-items: center;
  padding: 0;
  margin-left: auto;
  margin-top: -2px;
  transition: opacity 0.2s;
}
.main-header__toggle:hover {
  opacity: 0.8;
}
.main-header__toggle:active {
  opacity: 0.6;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

.main-header__toggle-no-js {
  display: none;
}

.main-header__toggle-line {
  display: block;
  background-color: #000000;
  width: 24px;
  height: 2px;
  transform: scale(1);
}
@media (min-width: 768px) {
  .main-header__toggle-line {
    display: none;
  }
}

.main-header__toggle::before,
.main-header__toggle::after {
  content: "";
  position: absolute;
  height: 2px;
  width: 100%;
  transition: all 0.3s ease 0s;
  background-color: #000000;
}

.main-header__toggle::before {
  top: 0;
}

.main-header__toggle::after {
  bottom: 0;
}

.main-header__toggle.main-header__toggle--active .main-header__toggle-line {
  transform: scale(0);
}

.main-header__toggle.main-header__toggle--active::before {
  top: 50%;
  transform: rotate(-47deg) translate(0, -50%);
}

.main-header__toggle.main-header__toggle--active::after {
  bottom: 50%;
  transform: rotate(47deg) translate(0, 50%);
}

