.main-nav {
  @media (min-width: $tablet-width) {
    max-width: 500px;
  }

  @media (min-width: $desktop-width) {
    max-width: 50%;
    padding-left: 10px;
  }
}

.main-nav--closed {
  display: none;

  @media (min-width: $tablet-width) {
    display: block;
  }
}

.main-nav--opened {
  position: absolute;
  z-index: 1;
  right: 0;
  left: 0;
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    position: initial;
    background-color: transparent;
  }
}

.main-nav--no-js {
  position: static;
  margin: 0 -20px;
}

.main-nav__list {
  @include reset-list;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.main-nav__item {
  text-align: center;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    border: none;
    margin: 0;
  }
}

.main-nav__item:first-child {
  border-top: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    border: none;
  }
}

.main-nav__item--current {
  @media (min-width: $tablet-width) {
    position: relative;
  }
}

.main-nav__item--current::after {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    right: 4px;
    left: 29px;
    bottom: 4px;
    height: 2px;
    background-color: $base__background-color;
    z-index: -1;
  }

  @media (min-width: $desktop-width) {
    right: -2px;
    left: 34px;
    bottom: 5px;
  }
}

.main-nav__item--current-index-desktop::after {
  @media (min-width: $desktop-width) {
    background-color: $special__fonts-color;
  }
}

.main-nav__link {
  @include fonts(20px, 20px);
  max-width: 320px;
  margin: 0 auto;
  padding: 22px 15px 23px;
  display: block;
  text-transform: uppercase;
  text-decoration: none;
  transition: opacity 0.2s;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    @include fonts(18px, 24px);
    margin-left: 25px;
    padding: 14px 4px 12px;
  }

  @media (min-width: $desktop-width) {
    @include fonts(20px, 30px);
    margin-left: 23px;
    padding: 12px 0 12px 12px;
  }
}

.main-nav__link--index-desktop {
  @media (min-width: $desktop-width) {
    color: $special__fonts-color;
  }
}
